.todolist {
    background-color: rgb(251, 247, 255);;
    width: 230px;
    height: fit-content;
    min-height: 150px;
    margin: 30px 25px;
    padding: 15px;
    border-radius: 20px;
    border: 3px solid rgba(167, 113, 255, 0.8);
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}

.todolistHeader {
    margin-bottom: 15px;
    display: flex;
    flex-direction: column;
}

.todolistHeader h2 {
    text-decoration: underline 2px rgba(167, 113, 255, 0.8);
}

.todolistHeaderName{
    height: 52px;
    display: flex;
    flex-direction: row;
    align-items: center;
}

.deleteTodolistButton {
    height: 30px;
    width: 30px;
    display: flex;
    justify-content: center;
    align-items: center;
    font-family: Garamond;
    border: none;
    padding-left: 18px;
    background: none;
    color: rgb(114, 71, 185);
    box-shadow: none;
    font-size: 25px;
}

.todolistSettings {
    margin-top: 10px;
    display: flex;
    flex-direction: row;
    justify-content: space-around;
}

button {
    padding: 5px 15px;
    font-family: Serif;
    font-size: 17px;
    font-weight: unset;
    border-radius: 30px;
    border: 2px solid rgba(167, 113, 255, 0.7);
    background-color: #fff;
    box-shadow: 0 0 5px rgba(167, 113, 255, 0.7);
}

button:hover {
    box-shadow: 0 0 10px rgba(167, 113, 255, 0.9);
}

button:active {
    color: rgb(159, 89, 255);
    border: 2px solid rgba(167, 113, 255, 1);
    box-shadow: 0 0 10px rgba(167, 113, 255, 0.9);
}

.filter {
    background-color: rgb(215, 190, 255);
}

.error {
    outline: none;
    border: 1px solid rgb(255, 0, 127);
}

.errorMessage {
    margin-top: 5px;
    color: rgb(255, 0, 127);
}
