.App {
    background: linear-gradient(rgb(255, 255, 255), white);
}

.appHeader {
    height: 60px;
    width: 100%;
    font-size: 23px;
    display: flex;
    justify-content: center;
    align-items: center;
    background: linear-gradient(rgb(167, 113, 255), rgba(255, 255, 255, 0.01));
    margin: 0;
    margin-bottom: 20px;
    color: white;
    font-weight: 900;
    -webkit-text-stroke-color: black;
    -webkit-text-stroke-width: 1px;
    position: fixed;
    pointer-events: none;
}

