.AddItemForm {
    display: flex;
    justify-content: center;
    align-items: start;
}

.AddTodolistBlock {
    padding-top: 78px;
}

.AddTaskBlock {
    justify-content: start;
}


.AddItemForm input {
    height: 25px;
    width: 150px;
    border-radius: 5px;
    border: 2px solid rgb(167, 113, 255);
    padding: 2px 5px;
    font-family: Serif;
    font-size: large;
    margin-right: 10px;
}

.AddItemForm button {
    padding: 5px 8px;
    font-size: 17px;
}

.AddItemForm button.AddTaskBlock {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 30px;
    width: 30px;
}

.addTaskField {
    margin-top: 0px;
    display: flex;
    align-items: center;
}

.taskAddInput {
    height: 25px;
    width: 150px;
    border-radius: 5px;
    border: 2px solid rgb(167, 113, 255);
    padding: 2px 5px;
    font-family: Serif;
    font-size: large;
}

.taskAddButton {
    font-weight: bold;
    margin-left: 10px;
    padding: 3px 8px;
    height: 30px;
    width: 30px;
}


.AddItemForm input.error {
    border: 2px solid rgb(255, 0, 127);
}

.errorMessage {
    margin-top: 5px;
    color: rgb(255, 0, 127);
}